.swiper-button-next,
.swiper-button-prev {
  padding: 0;
  background-color: transparent;
  border: none;
  font-family: "Poppins", "Tahoma", sans-serif;
  position: absolute;
  top: 50px;
  z-index: 10; }
  .swiper-button-next::after,
  .swiper-button-prev::after {
    content: ""; }

.swiper-button-prev {
  left: 30px; }

.swiper-button-next {
  right: 30px;
  transform: rotate(180deg); }

.swiper-pagination {
  bottom: -50px; }

.swiper-pagination-bullet {
  font-size: 1.6rem;
  line-height: 130%;
  font-weight: 500;
  margin: 0;
  padding: 0;
  width: 14px;
  height: 20px;
  display: inline-block;
  font-family: "Poppins", "Tahoma", sans-serif;
  color: #9b9997;
  background-color: transparent;
  text-align: center;
  border: 0;
  box-shadow: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }
  .swiper-pagination-bullet-active {
    color: #1d1613; }

@media (max-width: 1023px) {
  .swiper-slide {
    max-width: 324px; } }

@media (max-width: 767px) {
  .swiper-slide {
    max-width: 130px; } }

.catalog {
  margin-bottom: 0; }

.catalog__container {
  display: grid;
  grid-template-columns: 250px 1fr;
  grid-column-gap: 50px;
  grid-template-areas: "filter list" ". controls"; }

.catalog__button {
  display: none; }

.catalog__filter {
  grid-area: filter; }

.catalog__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 40px;
  grid-area: list;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 40px; }

.catalog__controls {
  padding-top: 40px;
  padding-bottom: 80px;
  grid-area: controls;
  display: flex;
  justify-content: space-between; }
  .catalog__controls button {
    font-size: 16px;
    line-height: 130%;
    font-weight: 500;
    font-family: "Poppins", "Tahoma", sans-serif;
    color: #1d1613;
    border: none;
    background-color: transparent; }

.catalog__pages {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  gap: 15px; }

.catalog__page {
  color: #9b9997; }
  .catalog__page--active {
    color: #1d1613; }

.filter {
  position: relative;
  display: flex;
  flex-direction: column; }

.filter__close {
  position: absolute;
  top: -15px;
  right: -15px;
  width: 30px;
  height: 30px;
  font-size: 0;
  border-radius: 50%;
  background-color: #000000;
  border: none;
  display: none; }

.filter fieldset {
  padding: 70px 25px 28px;
  position: relative;
  border: 1px solid #a87b62;
  border-bottom: none; }
  .filter fieldset:last-of-type {
    border-bottom: 1px solid #a87b62;
    margin-bottom: 35px;
    min-height: 150px; }
  .filter fieldset legend {
    position: absolute;
    top: 28px; }

.filter__group {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  gap: 15px; }

.filter__item {
  position: relative; }

.filter__item input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  position: absolute; }
  .filter__item input:checked + label::before {
    background-color: #a87b62; }

.filter__item label {
  font-size: 15px;
  line-height: 15px;
  font-weight: 400;
  position: relative;
  padding-left: 24px; }
  .filter__item label::before {
    position: absolute;
    content: "";
    width: 15px;
    height: 15px;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    border: 1px solid #a87b62; }

.filter__range {
  position: relative;
  height: 2px;
  background-color: #a87b62; }

.filter__range-between {
  width: 100%;
  height: 2px;
  background-color: #a87b62;
  margin-bottom: 30px; }

.filter__range-button-1,
.filter__range-button-2 {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #a87b62;
  border: none;
  cursor: pointer; }

.filter__range {
  position: relative; }

.filter__range-num {
  display: flex;
  justify-content: space-between;
  width: 140px;
  margin: 0 auto; }
  .filter__range-num span {
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
    font-family: "Poppins", "Tahoma", sans-serif;
    color: #1d1613; }
  .filter__range-num input {
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
    font-family: "Poppins", "Tahoma", sans-serif;
    color: #1d1613;
    border: none; }

.filter button[type="submit"],
.filter button[type="reset"] {
  font-size: 16px;
  line-height: 130%;
  font-weight: 500;
  padding: 28px;
  border: none;
  font-family: "Poppins", "Tahoma", sans-serif;
  display: block;
  cursor: pointer;
  margin-bottom: 25px;
  text-align: center;
  background-color: #a87b62;
  color: #ffffff; }

.filter button[type="reset"] {
  background-color: #ffffff;
  color: #a87b62;
  border: 1px solid #a87b62; }
