@font-face {
    font-family: "Poppins";
    font-display: swap;
    font-weight: 400;
    font-style: normal;
    src: local("Poppins"), url("../fonts/poppins-regular.woff2") format("woff2"),
        url("../fonts/poppins-regular.woff") format("woff");
}

@font-face {
    font-family: "Poppins";
    font-display: swap;
    font-weight: 500;
    font-style: normal;
    src: local("Poppins"), url("../fonts/poppins-medium.woff2") format("woff2"),
        url("../fonts/poppins-medium.woff") format("woff");
}

@font-face {
    font-family: "Poppins";
    font-display: swap;
    font-weight: 700;
    font-style: normal;
    src: local("Poppins"), url("../fonts/poppins-bold.woff2") format("woff2"),
        url("../fonts/poppins-bold.woff") format("woff");
}

@font-face {
    font-family: "Cormorant Garamond";
    font-display: swap;
    font-weight: 400;
    font-style: italic;
    src: local("Cormorant Garamond"),
        url("../fonts/cormorant-garamond-italic.woff2") format("woff2"),
        url("../fonts/cormorant-garamond-italic.woff") format("woff");
}

@font-face {
    font-family: "Cormorant Garamond";
    font-display: swap;
    font-weight: 600;
    font-style: normal;
    src: local("Cormorant Garamond"),
        url("../fonts/cormorant-garamond-semibold.woff2") format("woff2"),
        url("../fonts/cormorant-garamond-semibold.woff") format("woff");
}

@font-face {
    font-family: "Cormorant Garamond";
    font-display: swap;
    font-weight: 700;
    font-style: normal;
    src: local("Cormorant Garamond"),
        url("../fonts/cormorant-garamond-bold.woff2") format("woff2"),
        url("../fonts/cormorant-garamond-bold.woff") format("woff");
}
